import {
    EuiFlexGroup,
    EuiFlexItem,
    EuiSplitPanel,
    EuiTitle
} from '@elastic/eui';
import { useUpdateEffect } from 'ahooks';
import { animated, useSpring } from 'react-spring';

import './index.scss';
import Styles from './index.scss';

import { BLACK, WHITE } from '@modules/constants';
import { usePlayerCtrl } from '@modules/control';

const heartbeatSize = parseInt(Styles.heartbeatSize);
const switchInitStyle = {
    width: 0,
    height: heartbeatSize,
    opacity: 0
};
const switchFinalStyle = {
    width: heartbeatSize * 1.5,
    height: heartbeatSize,
    opacity: 1
};

const midSize = parseInt(Styles.middleLineSize);
const blackSidePoints = {
    points: `${midSize / 4},0
            ${midSize / 4 * 3},${midSize / 2}
            ${midSize / 4},${midSize}`
};
const midSidePoints = {
    points: `${midSize / 2},0
            ${midSize / 2},${midSize / 2}
            ${midSize / 2},${midSize}`
};
const whiteSidePoints = {
    points: `${midSize / 4 * 3},0
            ${midSize / 4},${midSize / 2}
            ${midSize / 4 * 3},${midSize}`
};

const HeartbeatSpot = ({
    color = 'transparent',
    cx = 0,
    cy = 0
}) => {
    const heartbeatStyle = useSpring({
        loop: { reverse: true },
        from: { r: Styles.heartbeatSR },
        to: { r: Styles.heartbeatLR }
    });

    return (
        <animated.circle
            cx={cx}
            cy={cy}
            style={{ fill: color }}
            {...heartbeatStyle}
        />
    );
};

const ScoreBar = ({
    currentPlayer = BLACK,
    isDone = true,
    blackScore = 0,
    whiteScore = 0,
    blackName = 'Player A',
    whiteName = 'Player B'
}) => {
    const [whiteStyle, whiteCtrl] = useSpring(() => switchInitStyle);
    const [blackStyle, blackCtrl] = useSpring(() => switchInitStyle);
    const [{ points }, midCtrl] = useSpring(() => midSidePoints);
    const [{ ptsBlack, ptsWhite }, ptsCtrl] = useSpring(() => ({
        ptsBlack: blackScore,
        ptsWhite: whiteScore
    }));

    const [curPlayer, togglePlayer] = usePlayerCtrl(
        () => {
            blackCtrl.start(switchFinalStyle);
            whiteCtrl.start(switchInitStyle);
            midCtrl.start(blackSidePoints);
        },
        () => {
            whiteCtrl.start(switchFinalStyle);
            blackCtrl.start(switchInitStyle);
            midCtrl.start(whiteSidePoints);
        }
    );

    useUpdateEffect(() => {
        togglePlayer(currentPlayer);
    }, [currentPlayer]);
    useUpdateEffect(() => {
        ptsCtrl.start({
            ptsBlack: blackScore,
            ptsWhite: whiteScore
        });
    }, [blackScore, whiteScore]);
    useUpdateEffect(() => {
        if (isDone) {
            whiteCtrl.start(switchInitStyle);
            blackCtrl.start(switchInitStyle);
            midCtrl.start(midSidePoints);
        } else if (curPlayer === BLACK) {
            blackCtrl.start(switchFinalStyle);
            whiteCtrl.start(switchInitStyle);
            midCtrl.start(blackSidePoints);
        } else if (curPlayer === WHITE) {
            whiteCtrl.start(switchFinalStyle);
            blackCtrl.start(switchInitStyle);
            midCtrl.start(whiteSidePoints);
        }
    }, [isDone]);

    return (
        <EuiSplitPanel.Outer direction='row'>
            <EuiSplitPanel.Inner className='black-item left-align'>
                <animated.svg style={blackStyle}>
                    {curPlayer === BLACK && (
                        <HeartbeatSpot
                            cx={heartbeatSize / 2}
                            cy={heartbeatSize / 2}
                            color={Styles.white}
                        />
                    )}
                </animated.svg>
                <EuiTitle size='s'>
                    <h3 className='white-text'>
                        {blackName}
                    </h3>
                </EuiTitle>
            </EuiSplitPanel.Inner>
            <EuiSplitPanel.Inner paddingSize='none'>
                <EuiFlexGroup
                    gutterSize='none'
                    responsive={false}
                >
                    <EuiFlexItem className='black-item right-align'>
                        <EuiTitle size='m'>
                            <animated.h2 className='white-text'>
                                {ptsBlack.to(t => t.toFixed(0))}
                            </animated.h2>
                        </EuiTitle>
                    </EuiFlexItem>
                    <EuiFlexItem
                        className='middle-line'
                        grow={false}
                    >
                        <svg style={{ height: midSize, width: midSize }}>
                            <animated.polygon
                                points={points.to(p => `0,0 ${p} 0,${midSize}`)}
                                style={{ fill: Styles.black }}
                            />
                            <animated.polygon
                                points={points.to(p => `${midSize},0 ${p} ${midSize},${midSize}`)}
                                style={{ fill: Styles.white }}
                            />
                        </svg>
                    </EuiFlexItem>
                    <EuiFlexItem className='white-item left-align'>
                        <EuiTitle size='m'>
                            <animated.h2 className='black-text'>
                                {ptsWhite.to(t => t.toFixed(0))}
                            </animated.h2>
                        </EuiTitle>
                    </EuiFlexItem>
                </EuiFlexGroup>
            </EuiSplitPanel.Inner>
            <EuiSplitPanel.Inner className='white-item right-align'>
                <EuiTitle size='s'>
                    <h3 className='black-text'>
                        {whiteName}
                    </h3>
                </EuiTitle>
                <animated.svg style={whiteStyle}>
                    {curPlayer === WHITE && (
                        <HeartbeatSpot
                            cx={heartbeatSize}
                            cy={heartbeatSize / 2}
                            color={Styles.black}
                        />
                    )}
                </animated.svg>
            </EuiSplitPanel.Inner>
        </EuiSplitPanel.Outer>
    );
};

export default ScoreBar;