import {
    EuiCard,
    EuiText
} from '@elastic/eui';
import { useUpdateEffect, useSafeState } from 'ahooks';
import { animated, useSpring } from 'react-spring';
import _ from 'lodash';

import './index.scss';
import Styles from './index.scss';

import { BLACK, WHITE, CORE } from '@modules/constants';
import { pieceImgParser, targetImgParser } from '@modules/parser';

const BlackStyle = { backgroundColor: Styles.black };
const MiddleStyle = { backgroundColor: Styles.middle };
const WhiteStyle = { backgroundColor: Styles.white };
const targetBoxStyle = {
    opacity: 1,
    scale: 1
};
const noTargetStyle = {
    opacity: 0,
    scale: 0.8
};

const bgParser = (owner = null) => {
    switch (owner) {
        case BLACK:
            return BlackStyle;
        case WHITE:
            return WhiteStyle;
        case null: default:
            return MiddleStyle;
    }
};

const Piece = ({
    piece = null,
    active = null,
    target = null,
    owner = null,
    currentPlayer = null,
    id = null,
    hint = null,
    onClick = () => { }
}) => {
    const [targetImg, setTargetImg] = useSafeState(targetImgParser(target, currentPlayer));
    const [bgStyle, bgCtl] = useSpring(() => bgParser(owner));
    const [tbStyle, tbCtl] = useSpring(() => target ? targetBoxStyle : noTargetStyle);

    const judgeTarget = () => !(piece?.type === CORE &&
        (currentPlayer === piece?.owner || owner !== currentPlayer));

    useUpdateEffect(() => {
        bgCtl.start(bgParser(owner));
    }, [owner]);

    useUpdateEffect(() => {
        if (target && judgeTarget()) {
            setTargetImg(targetImgParser(target, currentPlayer));
            tbCtl.start(targetBoxStyle);
        } else {
            tbCtl.start(noTargetStyle);
        }
    }, [target]);

    return (
        <animated.div
            style={bgStyle}
            className='piece-animator'
            id={id}
        >
            <EuiCard
                onClick={() => onClick()}
                title={piece ? (
                    <animated.img
                        className='piece'
                        src={pieceImgParser(piece)}
                    />
                ) : ''}
                className={`piece-container ${active ? 'active' : ''}`}
                paddingSize='none'
            >
                <animated.img
                    className='target-box'
                    src={targetImg}
                    style={tbStyle}
                />
                {_.isNumber(hint) && hint !== 0 && (
                    <EuiText className={`${hint >= 0 ? 'black' : 'white'}-text pts-hint`}>
                        <b>{Math.abs(hint)}</b>
                    </EuiText>
                )}
            </EuiCard>
        </animated.div>
    );
};

export default Piece;