import {
    useToggle,
    useUpdateEffect,
    useMount,
    useSafeState
} from 'ahooks';
import _ from 'lodash';

import { BLACK, WHITE, RED, YELLOW, PURPLE, GREEN, GRAY, BLUE } from '@modules/constants';

const initCores = {
    [RED]: 6,
    [BLUE]: 6,
    [GREEN]: 6,
    [YELLOW]: 6,
    [PURPLE]: 6,
    [GRAY]: 6
};

const anotherPlayer = (player = BLACK) => {
    if (player === BLACK) {
        return WHITE;
    } else if (player === WHITE) {
        return BLACK;
    } else {
        return null;
    }
};

export const usePlayerCtrl = (
    onBlackTurn = () => { },
    onWhiteTurn = () => { },
    initPlayer = BLACK
) => {
    const [curPlayer, { toggle, setLeft, setRight }] = useToggle(initPlayer, anotherPlayer(initPlayer));

    useUpdateEffect(() => {
        if (curPlayer === BLACK) {
            onBlackTurn();
        } else if (curPlayer === WHITE) {
            onWhiteTurn();
        }
    }, [curPlayer]);

    useMount(() => {
        if (initPlayer === BLACK) {
            onBlackTurn();
        } else if (initPlayer === WHITE) {
            onWhiteTurn();
        } else {
            console.err(`Unknown Player Type: ${initPlayer}`);
        }
    });

    const togglePlayer = (player) => {
        if (player === initPlayer) {
            setLeft();
        } else if (player === anotherPlayer(initPlayer)) {
            setRight();
        } else {
            toggle();
        }
    };

    return [curPlayer, togglePlayer];
};

export const useRunesGenerator = () => {
    const [runes, setRunes] = useSafeState([]);

    const generate = () => setRunes(
        _.chunk(_.shuffle(_.flattenDeep(
            _.map(initCores, (v, k) => _.times(
                v, () => ({ type: k, enable: true })
            ))
        )), 4)
    );

    useMount(() => {
        generate();
    });

    return [runes, generate, setRunes];
};
