import {
    EuiTitle,
    EuiPanel,
    EuiText
} from '@elastic/eui';
import {
    useMount,
    useToggle,
    useSafeState,
    useUpdateEffect,
    useRafInterval
} from "ahooks";
import _ from "lodash";

import { getClassName } from '@modules/parser';

const getTimeStr = (interval = 0) => {
    const [hours, minutes, seconds] = [
        _.padStart(_.floor(interval / 3600), 2, '0'),
        _.padStart(_.floor(interval / 60), 2, '0'),
        _.padStart(interval % 60, 2, '0')
    ];

    return `${hours}:${minutes}:${seconds}`;
};

const Timer = ({
    currentPlayer = null,
    player = null,
    locked = false
}) => {
    const [timer, timerCtrl] = useToggle(null, 1000);
    const [time, setTime] = useSafeState(0);
    const [styles, setStyles] = useSafeState(getClassName(player));

    useRafInterval(() => setTime(t => t + 1), timer);

    useMount(() => {
        if (!locked && currentPlayer === player) {
            timerCtrl.setRight();
        }
    });

    useUpdateEffect(() => {
        setStyles(getClassName(player));
        if (currentPlayer === player) {
            timerCtrl.setRight();
        } else {
            timerCtrl.setLeft();
        }
    }, [player, currentPlayer]);
    
    useUpdateEffect(() => {
        if (locked) {
            timerCtrl.setLeft();
        } else if (currentPlayer === player) {
            timerCtrl.setRight();
        }
    }, [locked]);

    return (
        <EuiPanel className={styles[0]}>
            <EuiText textAlign='center'>
                <EuiTitle size='s'>
                    <h3 className={styles[1]}>
                        {getTimeStr(time)}
                    </h3>
                </EuiTitle>
            </EuiText>
        </EuiPanel>
    );
};

export default Timer;