import React from 'react';
import ReactDOM from 'react-dom/client';
import {
    EuiProvider,
    EuiPage,
    EuiPageBody,
    EuiPageContent,
    EuiPageContentBody
} from '@elastic/eui';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';

import './index.scss';
import Styles from './index.scss';
import '@modules/icons';

import GamePage from '@pages/game';

const root = ReactDOM.createRoot(document.getElementById('root'));

root.render(
    // <React.StrictMode>
        <EuiProvider colorMode='LIGHT'>
            <EuiPage paddingSize='none'>
                <EuiPageBody className='page-container'>
                    <EuiPageContent
                        hasBorder={false}
                        hasShadow={false}
                        paddingSize='l'
                        verticalPosition='center'
                        borderRadius='none'
                        color='subdued'
                    >
                        <EuiPageContentBody restrictWidth={Styles.pageWidth}>
                            <BrowserRouter>
                                <Routes>
                                    <Route
                                        path='local'
                                        element={<GamePage />}
                                    />
                                    <Route
                                        path='online/:roomId'
                                        element={<GamePage />}
                                    />
                                    <Route
                                        path='online/:roomId/:role'
                                        element={<GamePage />}
                                    />
                                    <Route
                                        path='*'
                                        element={(
                                            <Navigate
                                                to='local'
                                                replace={true}
                                            />
                                        )}
                                    />
                                </Routes>
                            </BrowserRouter>
                        </EuiPageContentBody>
                    </EuiPageContent>
                </EuiPageBody>
            </EuiPage>
        </EuiProvider>
    // </React.StrictMode>
);
