import {
    EuiWrappingPopover,
    EuiKeyPadMenu,
    EuiKeyPadMenuItem,
    EuiImage,
    EuiPopoverTitle,
    EuiPopoverFooter
} from '@elastic/eui';

import './index.scss';
import Styles from './index.scss';

import { NORMAL, INCLUSIVE, EXCLUSIVE } from '@modules/constants';
import { pieceImgParser } from '@modules/parser';

const Upgrader = ({
    anchor = null,
    upgrades = [false, false, false],
    piece = null,
    selectCallback = (upgrade) => { },
    onClose = () => { }
}) => (
    <EuiWrappingPopover
        button={anchor}
        isOpen={anchor ? true : false}
        closePopover={() => onClose()}
        anchorPosition='upCenter'
        panelPaddingSize='s'
    >
        <EuiPopoverTitle>
            Promotion Available
        </EuiPopoverTitle>
        <EuiKeyPadMenu className='upgrader-pad'>
            <EuiKeyPadMenuItem
                label='Inclusive'
                disabled={!upgrades[0]}
                onClick={() => selectCallback(INCLUSIVE)}
            >
                <EuiImage
                    size={Styles.coreSize}
                    src={pieceImgParser({ ...piece, upgrade: INCLUSIVE })}
                    alt='Inclusive'
                />
            </EuiKeyPadMenuItem>
            <EuiKeyPadMenuItem
                label='Normal'
                disabled={!upgrades[1]}
                onClick={() => selectCallback(NORMAL)}
            >
                <EuiImage
                    size={Styles.coreSize}
                    src={pieceImgParser({ ...piece, upgrade: NORMAL })}
                    alt='Normal'
                />
            </EuiKeyPadMenuItem>
            <EuiKeyPadMenuItem
                label='Exclusive'
                disabled={!upgrades[2]}
                onClick={() => selectCallback(EXCLUSIVE)}
            >
                <EuiImage
                    size={Styles.coreSize}
                    src={pieceImgParser({ ...piece, upgrade: EXCLUSIVE })}
                    alt='Exclusive'
                />
            </EuiKeyPadMenuItem>
        </EuiKeyPadMenu>
        <EuiPopoverFooter>
            Choose a Direction for Upgrade
        </EuiPopoverFooter>
    </EuiWrappingPopover>
);

export default Upgrader;