import {
    EuiCard, EuiFlexGrid, EuiFlexItem
} from "@elastic/eui";
import { animated, useSpring } from "react-spring";
import { useUpdateEffect } from "ahooks";

import './index.scss';
import Styles from './index.scss';
import RuneImg from '@assets/images/rune.png';

import { BLACK, WHITE } from '@modules/constants';
import { getClassName, coreImgParser } from '@modules/parser';

const enableStyle = { opacity: 1 };
const disableStyle = { opacity: 0.5 };
const BlackStyle = { backgroundColor: Styles.black };
const MiddleStyle = { backgroundColor: Styles.middle };
const WhiteStyle = { backgroundColor: Styles.white };

const bgParser = (owner = null) => {
    switch (owner) {
        case BLACK:
            return BlackStyle;
        case WHITE:
            return WhiteStyle;
        case null: default:
            return MiddleStyle;
    }
};

const Rune = ({
    owner = null,
    cores = null,
    active = null,
    general = false,
    onClick = (index) => { }
}) => {
    const coreStyles = [
        useSpring(() => cores && cores[0].enable ? enableStyle : disableStyle),
        useSpring(() => cores && cores[1].enable ? enableStyle : disableStyle),
        useSpring(() => cores && cores[2].enable ? enableStyle : disableStyle),
        useSpring(() => cores && cores[3].enable ? enableStyle : disableStyle)
    ];
    const runeStyle = useSpring({
        loop: true,
        from: { rotate: '0deg', scale: 1 },
        to: [
            { rotate: '90deg', scale: 0.8 },
            { rotate: '90deg', scale: 1 }
        ]
    });

    const [bgStyle, bgCtl] = useSpring(() => bgParser(owner));

    useUpdateEffect(() => {
        cores?.forEach((core, i) => {
            coreStyles[i][1].start(core.enable ? enableStyle : disableStyle);
        });
    }, [cores]);

    useUpdateEffect(() => {
        bgCtl.start(bgParser(owner));
    }, [owner]);

    return (
        <animated.div
            style={bgStyle}
            className='rune-animator'
        >
            <EuiCard
                title={cores ? (
                    <EuiFlexGrid
                        className={`core-container ${general ? 'general' : ''}`}
                        gutterSize='m'
                        columns={2}
                    >
                        {cores.map((core, i) => (
                            <EuiFlexItem
                                className={`core-img-container ${active === i ? 'active' : ''}`}
                                key={i}
                            >
                                <animated.img
                                    style={coreStyles[i][0]}
                                    className='core-img'
                                    src={coreImgParser(core?.type)}
                                    onClick={() => core?.enable && onClick(i)}
                                />
                            </EuiFlexItem>
                        ))}
                    </EuiFlexGrid>
                ) : (
                    <animated.img
                        className='rune-picking-img'
                        src={RuneImg}
                        style={runeStyle}
                    />
                )}
                className={`rune-container ${getClassName(owner)[0]}`}
                paddingSize='none'
                onClick={() => { }}
            />
        </animated.div>
    );
};

export default Rune;